import { useEffect, useState } from "react";
import TelegramWebApp from "@twa-dev/sdk"; // : npm install @twa-dev/sdk
import Chat from "./Chat";

function App() {
  const [user, setUser] = useState(null);

  useEffect(() => {
    TelegramWebApp.ready();

    const initData = TelegramWebApp.initDataUnsafe;
    if (initData?.user) {
      setUser(initData.user);
    } else {
      console.warn(" . ,   Web App  Telegram.");
    }

    //      Telegram Web App
    document.body.style.backgroundColor = TelegramWebApp.themeParams.bg_color || "#ffffff";
    document.body.style.color = TelegramWebApp.themeParams.text_color || "#000000";
  }, []);

  return (
    <div style={{ padding: "20px", textAlign: "center" }}>
      {user ? (
        <>
          <Chat user={user} />
        </>
      ) : (
        <p> . ,  Web App  Telegram.</p>
      )}
    </div>
  );
}

export default App;